function inlineSlider() {
    var swiper = new Swiper('.inline-swiper .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        loop: true,
        allowTouchMove: false,
        autoplay: {
            delay: 3000,
            reverseDirection: true
        },
    })
}
inlineSlider()